const ADD_NEWS = "ADD_NEWS";
const UPDATE_NEWS = "UPDATE_NEWS";
const CLEAR_ALL = "CLEAR_ALL";

const initialState = {
  posts: [
    { id: "1", postedNews: "Для подключения относительного пути для картинок можете использовать: <img src={require('./1.jpg')}/>" },
    { id: "2", postedNews: "Чтобы сделать активной ссылку при переходе на нее: <NavLink to='#' activeClassName={styles.activeLink}>Example</NavLink>; for CSS:.activeLink {color:gold;}" }
  ],
  newPosts: " ",
}

const blogReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_NEWS:
      return {
        ...state, 
        posts: [...state.posts, { id: 3, postedNews: state.newPosts }], 
        newPosts: " ", 
      };
    case UPDATE_NEWS:
      return {
        ...state,
        newPosts: action.body
      };
    case CLEAR_ALL:
      return {
        ...state,
        posts: [],
        newPosts: " ",
      };
    default:
      return state;
  }
}

export const addNewsCreator = () => {
  return {
    type: ADD_NEWS
  }
}

export const updateNewsCreator = (body) => {
  return {
    type: UPDATE_NEWS,
    body: body
  }
}

export const ClearAllCreator = () => {
  return {
    type: CLEAR_ALL
  }
}

export default blogReducer